<template>
    <div class="index">
        <div id="t1">{{t1}}</div>
        <div id="t2" v-on:click="clickMe">{{t2}}</div>
        <div id="pos">{{pos}}</div>
        <button v-on:click="findPosition">Find position</button>
    </div>
</template>

<script>
    const axios = require('axios');
    export default {
        name: 'Index',
        data() {
            return {
                t1: 'This is div 1',
                t2: 'This is div 2',
                pos: 'a',
            }
        },
        created() {
            this.doCheck();
        },
        methods: {
            clickMe: function(){
                this.doCheck();
            },
            checkNetwork: async function(){
                let result = await fetch('http://minhthien309.now.sh',{
                                mode: 'no-cors'
                            }).then(response => {
                                return response;
                            }).catch(error => {
                                console.log(error);
                            });
                return result;
            },
            doCheck: async function(){
                if(window.matchMedia('(display-mode: standalone)').matches){
                    let result = await this.checkNetwork();
                    if(result == undefined){
                        this.t2 = 'Cannot connect!';
                    }
                    else{
                        this.t2 = 'Connect success';
                    }
                }
            },
            findPosition: async function(){
                let vue = this
                navigator.geolocation.getCurrentPosition(function(position) {

                    // Get the coordinates of the current position.
                    var lat = position.coords.latitude;
                    var lng = position.coords.longitude;
                    vue.pos = lat + "/" + lng;
                });
            }
        }
    }
</script>


